import { useState, useEffect } from 'react'
import { animated, useSpring } from 'react-spring'
import * as easings from 'd3-ease'
import PageNav from './pageNav'
import ProjOneBack from './proj-one.png'
import ProjectSection from './projects'

const Item = ({itemType, itemIndex, setPrev, fontSize, page, html}) => {

    const Projects = [{
            name: "travelaholic",
            roles: ["Front-End", "Back-End", "UI/UX"],
            description: ["When you travel, you meet new people, and cultures, go through different experiences (for better or worse), and perhaps even change your perspective on things and life as a whole. You meet new people and visit new places, so you learn and gain information, this shapes you into a better person.", "So what I wanted to make was a place where I could get suggestions for places I could visit as per what I would prefer to do. After a place was suggested, I would like to see the details for reference like local places and also the flight fares."],
            mainImage: "./projects/travelaholic/0.jpg",
            images: ["./projects/travelaholic/1.jpg", "./projects/travelaholic/2.jpg", "./projects/travelaholic/3.jpg", "./projects/travelaholic/4.jpg"],
            links: [{name: "Github", link: "https://github.com/prateekbose/travelaholic"},
                    {name: "Demo", link: "https://travelaholic.000webhostapp.com/"}],
            color: "#202020",
            text: "#ffffff"
        },
        {
            name: "hungri",
            roles: ["Front-End", "UI/UX"],
            description: ["This project is a Progressive Web App, which creates a platform to help the hungry by relocating the wasted and leftovers from a small house all the way to a large scale party", "We created a platform which utilizes the resources of the city municipality to implement a system to distribute leftovers and wasted food to the hungry and also use the wasted food for recyling."],
            mainImage: "./projects/hungri/0.jpg",
            images: ["./projects/hungri/1.jpg", "./projects/hungri/2.jpg", "./projects/hungri/3.jpg", "./projects/hungri/4.jpg"],
            links: [{name: "Github", link: "https://github.com/prateekbose/hungri-front-end"},
                    {name: "Demo", link: "https://hungri.vercel.app/"}],
            color: "#202020",
            text: "#ffffff"
        },
        {
            name: "mindfull",
            roles: ["Front-End", "UI/UX"],
            description: ["A platform is the cumulation of various resources in one place to help mental disorders and spread awareness regarding the issue, which is critical in this day and age.", "The design is minimalistic channelling the user's focus to the actual resources."],
            mainImage: "./projects/mindfull/0.JPG",
            images: ["./projects/mindfull/0.JPG", "./projects/mindfull/1.JPG", "./projects/mindfull/2.JPG"],
            links: [{name: "Github", link: "https://github.com/prateekbose/mindfull"},
                    {name: "Demo", link: "https://mindfullweb.vercel.app/"}],
            color: "#202020",
            text: "#ffffff"
        }]  
    const Works = [{
            name: "travelaholic",
            roles: ["Front-End", "Back-End", "UI/UX"],
            mainImage: "./projects/travelaholic/1.JPG",
            images: ["./projects/travelaholic/2.JPG","./projects/travelaholic/3.JPG"],
            color: "#202020",
            text: "#ffffff"
        },
        {
            name: "hungri",
            roles: ["Front-End", "UI/UX"],
            mainImage: "./projects/hungri/1.JPG",
            images: ["./projects/hungri/2.JPG","./projects/hungri/3.JPG"],
            color: "#202020",
            text: "#ffffff"
        }]  

    
    var item = {}
    var LinkActive = false

    if(itemType === "Projects"){
        item = Projects
        LinkActive = true
    } else {
        item = Works
    }


    const SectionAnimate = useSpring({
        from: {
            transform: 'translateX(-100vw)',
            opacity: 0
        },
        to: {
            transform: (page === "Item")?'translateX(0vw)':'translateX(-100vw)',
            opacity: (page === "Item")?1:0
        },
        delay: 150,
        config: {
            duration: 1200,
            easing: easings.easeCubic
        }
    })

    useEffect(() => {
        if(page === "Item"){
            html.classList.remove('no-scroll')
        }
    })

    return (
        <animated.div className="item-page" style={SectionAnimate}>            
            <PageNav prevPage={setPrev} page={page} pageName={"Item"} color={(Projects[itemIndex].text === "#ffffff")?"light":"dark"}/>
            <div className="item-image" style={{backgroundColor: Projects[itemIndex].color}}>
                <h1 style={{color: Projects[itemIndex].text}}>{item[itemIndex].name}</h1>
                <img src={item[itemIndex].mainImage} alt="Project Image"/>
            </div>
            <div className="item-details">
                <div className="header">
                    <h2>Roles</h2>
                    <ul>
                        {item[itemIndex].roles.map((item, index) => (
                            <li key={index}>{item}</li>
                        ))}
                    </ul>
                    {item[itemIndex].description.map((item, index) => (
                        <p key={index}>{item}</p>
                    ))}
                </div>
                <div className="item-links">
                {LinkActive?
                    Projects[itemIndex].links.map((item, index) => (
                        <a key={index} href={item.link} target="_blank" className="item-link">{item.name}</a>
                    ))
                    :
                    <div></div>
                }
                </div>
            </div>
            <div className="item-image-section">
                {item[itemIndex].images.map((item, index) => (
                    <div className="item-image" key={index} style={{backgroundColor: `${item[itemIndex].color}`}}>
                        <img src={item} alt="Project Image"/>
                    </div>
                ))}
            </div>
        </animated.div>
    )
}

export default Item