import React, {useState} from 'react'
import {animated, useSpring} from 'react-spring'
import * as easings from 'd3-ease'
import ProjOneBack from './projects/travelaholic/1.JPG'
import ProjTwoBack from'./projects/hungri/0.jpg'
import ProjThreeBack from'./projects/mindfull/0.JPG'
import PageNav from './pageNav'

const ProjectSection = ({fontSize, page, prevPage, Projects, nextPage, setItemIndex}) => {  
    const ProjectImage = [ProjOneBack, ProjTwoBack, ProjThreeBack, ProjTwoBack]

    const details = ["Discover places to travel to, get suggestions for places you could visit as per what you would prefer to do.", "Utilize the resources of the city municipality to implement a system to distribute leftovers and wasted food to the hungry and for recyling.", "A platform is the cumulation of various resources in one place to help mental disorders and spread awareness regarding the issue, which is critical in this day and age.", ""]

    const [hoverIndex, setHoverIndex] = useState(null)

    const SectionAnimate = useSpring({
        from: {
            transform: 'translateX(-100vw)',
            opacity: 0
        },
        to: {
            transform: (page === "Projects")?'translateX(0vw)':'translateX(-100vw)',
            opacity: (page === "Projects")?1:0
        },
        delay: 150,
        config: {
            duration: 1200,
            easing: easings.easeCubic
        }
    })

    const ImageAnimate = (index) => useSpring({
        from: {
            width: 0,
            opacity: 0,
            height: (18*fontSize)
        },
        to: {
            width: (index === hoverIndex)?(32*fontSize):0,
            opacity: (index === hoverIndex)?1:0,
            height: (18*fontSize)
        },
        config: {
            duration: 450,
            easing: easings.easeCubicInOut
        }
    })   
    
    const HeaderAnimate = (index) => useSpring({
        from: {
            marginLeft: -10,
            opacity: 0
        },
        to: {
            marginLeft: (page === "Projects")?0:-10,
            opacity: (page === "Projects")?1:0 
        },
        delay: 1050 + (index*150),
        config: {
            duration: 450,
            easing: easings.easeCubic
        }
    })

    const openItem = () => {
        nextPage("Item")
        setItemIndex(hoverIndex)
    }

    return (        
        <animated.div className="project-section" style={SectionAnimate}>
            <PageNav prevPage={prevPage} page={page} pageName={"Projects"}/>
            <div className="project-item-section">
            {Projects.map((item, index) => (
                <animated.div className="project-item" style={HeaderAnimate(index)} onClick={openItem}>
                    <h1 onMouseEnter={() => setHoverIndex(index)} onMouseLeave={() => setHoverIndex(null)}>{item}</h1>
                    <p>{details[index]}</p>
                    <animated.img src={ProjectImage[index]} alt={`${item} Demo`} style={ImageAnimate(index)}/>
                </animated.div>
            ))}
            </div>
        </animated.div>
    )
}

export default ProjectSection